<div class="collections"><!-- Main Heading--><mainHeading h2="Explore Some Hot " span="Collections" h2Continue="In Market"></mainHeading><div class="content position-relative container"><div class="collections-container overflow-hidden"><div class="moving-container row flex-nowrap m-0 ms-auto me-auto" id="id"><div class="collection-item col-sm-6 col-lg-4 position-relative mb-5" v-for="collection in collections" :key="collection"><img class="rounded-top-4" src="@/assets/imgs/collection-01.jpg"><div class="text rounded-bottom-4 p-4"><h5 class="border-bottom pb-3">{{ collection.title }}</h5><div class="items-text d-flex justify-content-between flex-column mt-3"><div><p class="mb-0">Items In Collection:</p><h5>{{ collection.items }}</h5></div><div><p class="mb-0">Category:</p><h5>{{ collection.category }}</h5></div></div></div><button class="button main-button button-to-white w-75 position-absolute start-50 translate-middle">Explore Mutant</button></div></div></div><div class="arrows d-flex justify-content-between"><font-awesome-icon class="main-arrow arrow position-absolute top-50 start-0" :icon="['fas', 'chevron-left']" id="arrow-left" @click="arrowMoveItem($event)"></font-awesome-icon><font-awesome-icon class="main-arrow arrow position-absolute top-50 end-0" :icon="['fas', 'chevron-right']" id="arrow-right" @click="arrowMoveItem($event)"></font-awesome-icon></div></div></div>