<script>

export default {
  data: function () {
    return {
      links: [
        { to: "/", title: "Home" },
        { to: "/explore", title: "Explore" },
      ],
      scrollTopValue: 0,
      xSign: false,
      langClicked: false,
      currentLang: 'English',
    };
  },
  methods: {
    changeLang(event) {
      var langs = document.querySelectorAll(".lang");
      // Change Button Content To The Clicked Language
      this.currentLang = event.currentTarget.textContent;
      
      // Make All Languuges Appears
      langs.forEach((lang) => {
        lang.style.display = "block";
      })
      // Hide The Clicked One
      event.currentTarget.style.display = "none";

      // Change Page Direction
      if (event.currentTarget.getAttribute("id") == "Ar") {
        console.log("ar", event.currentTarget.dataset.dir)
      }
    }
  },
  created() {
    addEventListener("scroll", () => {
      this.scrollTopValue = document.documentElement.scrollTop;
    });
  },
};
</script>
