<div class="discover-items py-5"> <div class="container my-5"> <div class="upper my-5 pt-5"> <mainHeading h2="Discover Some Of Our" span="Items" h2Continue=""></mainHeading><form class="d-flex flex-column flex-lg-row gap-3 justify-content-center" @submit.prevent="preventSubmit"><input type="text" name="text" placeholder="Item title..." v-model="searchValue"><button class="select d-flex align-items-center justify-content-between column-gap-2 position-relative" type="button" @click="categClicked = !categClicked; closeOtherSelect()">{{categCurrent }} <font-awesome-icon class="select-arrow" :icon="['fas', 'angle-down']" :class="{'change-icon': categClicked}"></font-awesome-icon><ul class="categUl w-100 text-start m-0 position-absolute z-1 start-0 rounded" :class="{'show' : categClicked}"><li class="categ w-100 px-3 py-1" v-for="(Category, index) in ['All Categories', 'Music Art',  'Digital Art',  'Blockchain Art',  'Virtual Art']" :key="index" @click="changeCateg($event)" id="categ" :class="{'active': index == 0}"> {{ Category }}</li></ul></button><button class="select d-flex align-items-center justify-content-between column-gap-2 position-relative" type="button" @click="availbClicked = !availbClicked; closeOtherSelect()">{{availbCurrent}}<font-awesome-icon class="select-arrow" :icon="['fas', 'angle-down']" :class="{'change-icon': availbClicked}"></font-awesome-icon><ul class="availbUl w-100 text-start m-0 position-absolute z-1 start-0 rounded" :class="{'show' : availbClicked}"><li class="availb w-100 px-3 py-1" v-for="(aviability, index) in ['All Items','Available','Ending Soon', 'Coming Soon','Closed',]" :key="index" @click="changeAvailb($event)" id="availb" :class="{'active': index == 0}"> {{ aviability }}</li></ul></button><input class="button main-button button-to-white border-0" type="submit" value="Search" @click="searchItems()"></form></div><div class="items container text-center"><div class="row row-items"><div class="item double-item col-md-12 col-lg-6 text-white my-4 p-4 py-5 rounded-4 position-relative" v-show="items[0].show || items[1].show"><button class="double-button button main-button position-absolute start-50 translate-middle">Double Details</button><div class="row content"><div class="col-md-6" v-for="item in items.slice(0,2)" :key="items"> <div class="imgs position-relative"><img class="rounded-circle position-absolute start-50 translate-middle" src="@/assets/imgs/author.jpg"><img class="rounded-4" :src="item.imgSrc"></div><h5 class="title py-3 pb-4">{{ item.title }}</h5></div><div class="line mx-auto mb-4"></div><div class="col-6 col-lg-12"><p class="mb-0">Category</p><h5>{{ items[0].categ[0] }}</h5></div><div class="col-6 col-lg-12"><p class="mb-0">Ends In</p><h5>{{  isNaN(items[0].date[0]) ? 'Not specified' :  items[0].date[0].toLocaleDateString() }}</h5></div></div><button class="view-button button main-button button-to-white position-absolute start-50 translate-middle">View Details</button></div><div class="item col-md-6 col-lg-3 text-white my-4 p-4 py-5 rounded-4 position-relative" v-for="item in items.slice(2)" :key="items" v-show="item.show"><div class="imgs position-relative"> <img class="rounded-circle position-absolute start-50 translate-middle" src="@/assets/imgs/author.jpg"><img class="rounded-4" :src="item.imgSrc"></div><div class="info"><h5 class="title py-3 pb-4">{{ item.title }}</h5><div class="details mt-4 d-flex d-lg-block flex-lg-column align-items-center gap-5"><div class="me-5"><p class="mb-0">Category</p><h5>{{ item.categ[0] }}</h5></div><div><p class="mb-0">Ends In</p><h5>{{ isNaN(item.date[0]) ? 'Not specified' :  item.date[0].toLocaleDateString() }}</h5></div></div></div><button class="view-button button main-button button-to-white position-absolute start-50 translate-middle">View Details</button></div></div></div></div></div>