<div class="create-ntf py-5"><div class="container"><div class="heading d-flex flex-column flex-lg-row align-items-center align-items-lg-start justify-content-lg-between my-5 mt-lg-1"><mainHeading class="link-side white-line" h2="Create Your NFT &amp; Put It On The Market"></mainHeading><button class="button main-button button-to-white mt-2">Create Your NFT Now</button></div><div class="content row row-gap-4"><div class="item col-lg-4 text-center" v-for="item, index in Createitems" :key="item" :class="index === 0 || index === 1 ? 'item-border' : ''"><div class="img bg-white rounded-circle m-auto mb-4 d-flex align-items-center justify-content-center"><img class="w-50" :src="item.iconSrc"></div><h5 class="text-white fw-bold">{{ item.title }}</h5><p>{{ item.text }} <a>{{item.link}}</a> {{ item.textContinue }}</p></div></div></div></div>