<div class="current-market py-5"><!-- Main Heading--><mainHeading h2="" span="Items " h2Continue="Currently In The Market"></mainHeading><div class="buttons-container d-flex justify-content-center flex-wrap gap-3"><button class="button transparent-button button-to-main-color" v-for="button in Buttons" :key="button" :id="button.id" @click="changeId(button.id)" :class="currentId == button.id ? 'active' : ''">{{ button.value }}</button></div><div class="items-container container row py-5 m-auto"><div class="item col-lg-6 d-flex gap-4 text-white p-5 rounded-4" v-for="item in marketItems" :key="item" :class="item.isDisplay || currentId == 0 ? 'display-item' :''"><div class="img d-none d-md-block"> <img class="rounded-4" src="@/assets/imgs/market-01.jpg"></div><div class="text flex-grow-1"><div class="up-text pb-4"><h5 class="mb-4">{{ item.title }}</h5><div class="d-flex align-items-center column-gap-3">   <img class="rounded-circle" src="@/assets/imgs/author.jpg"><p class="mb-0 lh-base">Liberty Artist <br> <a>@libertyart</a></p></div></div><div class="down-text d-flex flex-lg-column justify-content-between py-4 mb-4"><div><p class="mb-1">Current Bid</p><h5>{{ item.currentBid }}</h5><span>{{ item.price }}</span></div><div><p class="mb-1">Ends In</p><h5>{{ item.endIn }}</h5><span>{{ item.date }}</span></div></div><a class="detail fw-bold">View Item Details</a></div></div></div></div>